"use client";
import { useState, useEffect } from "react";
import TopCustomers from "@/components/TopCustomers";
import LeastCustomers from "@/components/LeastCustomers";
import { Customer } from "@/components/Customer";

export default function CustomersPage() {
    const [customers, setCustomers] = useState<Customer[]>([]);
    const sortedCustomers = customers?.sort((a, b) => (a.sales?.length || 0) - (b.sales?.length || 0));

    // Top Customers and Least Customers
    const leastCustomers = sortedCustomers.slice(-5).reverse();

    useEffect(() => {
        fetch("/api/customers")
            .then((response) => response.json())
            .then((data) => setCustomers(data));
    }, []);

    return (
        <div>
            <TopCustomers customers={sortedCustomers} title="Top Customers" iconColor="text-green-500" />
            <LeastCustomers customers={leastCustomers} title="Least Active Customers" iconColor="text-red-500" />
        </div>
    );
}